body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pp-tabs {
  overflow: hidden; }

.pp-tab {
  display: inline-block;
  text-align: center;
  padding: 10px 0px;
  font-size: 16px;
  -webkit-transform: translateZ(0);
          transform: translateZ(0); }

.pp-iscroll-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .pp-iscroll-wrap::-webkit-scrollbar {
    display: none; }
  .pp-iscroll-wrap .pp-iscroll {
    position: absolute;
    width: 100%;
    z-index: 1; }
  .pp-iscroll-wrap .pp-iscroll-pull-tips {
    position: absolute;
    width: 100%;
    padding-top: 0.6rem;
    line-height: 0.5rem;
    font-size: 0.36rem;
    color: #cccccc;
    text-align: center;
    z-index: 0; }

.pp-tabList {
  width: 100%; }
  .pp-tabList .ink {
    position: absolute;
    bottom: 0;
    height: 1px;
    transition-duration: 0.2s;
    transition-timing-function: ease-in-out;
    border-radius: 5px; }

.pp-panelList {
  overflow: hidden; }

.pp-panel {
  display: block;
  float: left;
  height: 100%; }


.App {
  text-align: center;
  height: 1000px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.title {
  margin-top: 50px;
  padding-bottom: 5px;
  font-size: 25px;
  /* border-bottom: 1px solid; */
}
